import React, { useEffect, useState } from 'react';
import { Toast } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
// import './Notification.css';

const Notification = (props) =>{
    const [seconds,setSeconds] = useState(0);
    const [duration, setDuration] = useState("just now");
    const setSecondsHandler = () =>{
        // I DONT KNOW WHY THE NUMBERS NEEDS TO BE LIKE THIS. 
        // SEEMS LIKE THE INTERVAL ms DOESNT WORK
        const current = seconds;
        setSeconds(current+1);
        if(seconds<60000){
            setDuration("just now");
        }else if(seconds >= 60000){
            if(seconds >= 3600000){
                setDuration(Math.floor(seconds/3600000) + " hr ago");
            }else{
                setDuration(Math.floor(seconds/60000) + " mins ago");
            }
        }
    }

    const closeNotification = () => {
        setSeconds(0);
        props.setShow(false);
    }

    useEffect(()=>{
        // let myInterval = setInterval(setSecondsHandler(),1000);
        // return ()=> {
        //     clearInterval(myInterval);
        //   };
    })

    
    return(
        <Toast onClose={() => props.setShow(false)} show={props.show} delay={5000} autohide animation style={{
            position: 'absolute',
            top: 20,
            right: 20,
          }}>
            <Toast.Header>
              <img
                src="holder.js/20x20?text=%20"
                className="rounded mr-2"
                alt=""
              />
              <strong className="mr-auto">{props.payload.title}</strong>
              <small>{duration}</small>
            </Toast.Header>
            <Toast.Body>{props.payload.body}</Toast.Body>
          </Toast>
    )
}

export default Notification;