import React, { useState } from "react";
import "./Game.css";

const Game = (props) => {
    const [message, setMessage] = useState("What's your guess?");
    const [guess, setGuess] = useState("");
    const [restart, setRestart] = useState(true);

    const setGuessHandler = (enteredNumber)=>{
        const re = /^[0-9\b]+$/;

        if (enteredNumber === '' || re.test(enteredNumber)) {
            setMessage("What's your guess?");
            setGuess(enteredNumber);
        }
        
    }

    const checkNumber = () => {
        if(guess > props.number){
            if((guess-props.number)< 5){
                setMessage("Almost!! Guess lower");
            }else{
                setMessage("Lower number");
            }
        }else if(guess < props.number){
            if((props.number - guess) < 5){
                setMessage("You're close!! Try raising your number");
            }else{
                setMessage("Try a higher number");   
            }
        }else{
            setMessage("You guessed it! It's " + props.number);
            setRestart(false);
            navigator.serviceWorker.getRegistration("/")
                .then((registration)=>{
                    registration.showNotification("Congratulations",{
                        body:"You just guessed the number!"
                    })
                })
        }
    }

    const restartGame = () =>{
        props.generate();
        setMessage("What's your guess?");
        setGuess("");
        setRestart(true);
    }

  return (
    <div hidden={props.isVisible}>
      <p>( 1 - 99 )</p>
      <input className="input" type="text" maxLength="2" onChange={num => setGuessHandler(num.target.value)} value={guess}></input>
      <br />
      <button className="button" hidden={!restart} onClick={checkNumber}>Guess</button>
      <p><strong>{message}</strong></p>
      <button className="button" hidden={restart} onClick={restartGame}>Play again?</button>
    </div>
  );
};

export default Game;
