import React, { useState } from "react";
import "./Token.css";
import axios from 'axios';

const Token = (props) => {
  const [tkn,setTkn] = useState("");
  const [btnText, setBtnText] = useState("Show Firebase Token");
  const [textField, setTextField] = useState(true);
  const [copy, setCopy] = useState(true);
  const [copyMessage, setCopyMessage] = useState(true);
  const showTextField = () => {
    if (textField === true) {
      if(!navigator.onLine){
        setTkn("You are offline, cannot generate firebase token.");
      }else{
        setTkn(props.tkn);
      }
      setBtnText("Hide Firebase Token");
      setTextField(false);
      setCopy(false);
    } else {
      setBtnText("Show Firebase Token");
      setTextField(true);
      setCopy(true);
      setCopyMessage(true);
    }
  };
  const copyToClipboard = () => {
    navigator.clipboard.writeText(props.tkn);
    setCopyMessage(false);
  };

  const sendPush = () =>{
   
    axios.post("https://prototypepushserver.herokuapp.com/push/user",
    {
      title:"Hello there!",
      message:"I've been requested from reactjs",
      topic:"",
      token:tkn
    },{
      headers:{
        "Content-Type":"application/json"
      }
    }
    ).then((resp)=>{
      console.log(resp);
    }).then((resp)=>{
      console.log(resp);
    })
    // axios.get("http://localhost:8080/send").then((resp)=>{
    //   console.log(resp);
    // });
  }
  return (
    <div>
      <button className="button" onClick={showTextField}>{btnText}</button>
      <br />
      <textarea className="textarea" hidden={textField} value={tkn}></textarea>
      <br />
      <p className="warn" hidden={copyMessage}>
        Successfully copied!
      </p>
      <br />
      <button className="button" hidden={copy} onClick={copyToClipboard}>
        Copy
      </button>
      <button className="button" hidden={copy} onClick={sendPush}>Push Notif</button>
    </div>
  );
};

export default Token;
